<template>
  <div>
    <base-panel>
      <template slot="panel-head">
        <router-link to="/">主页</router-link>
        /
        <span>未读消息</span>
      </template>
      <template slot="panel-content"></template>
    </base-panel>
    <base-panel>
      <template slot="panel-head">
        <span>过往信息</span>
      </template>
      <template slot="panel-content">
        <div v-for="message in messages.has_read_messages" :key="message.id">
          <router-link :to="`/user/${message.author.loginname}`">{{
            message.author.loginname
          }}</router-link>
          {{ message.type === "reply" ? "回复了你的话题" : "在话题" }}
          <router-link :to="`/topic/${message.topic.id}`">{{
            message.topic.title
          }}</router-link>
          {{ message.type === "reply" ? "" : "@你了" }}
        </div>
      </template>
    </base-panel>
  </div>
</template>

<script>
import basePanel from "../components/layout/basePanel.vue";
import { getMessage, markAllMessage } from "../plugins/api";
export default {
  components: { basePanel },
  data() {
    return {
      messages: null,
    };
  },
  async created() {
    const res = await getMessage(sessionStorage.getItem("accesstoken"));
    console.log(res);
    this.messages = res.data;
    if (res.data.hasnot_read_messages.length) {
      await markAllMessage(sessionStorage.getItem("accesstoken"));
    }
  },
};
</script>

